<template>
   <div class="row justify-content-center container-fluid count" style="margin:0">
    <form class="col-md-6" @submit.prevent="payOrder">
      <table class="table">
        <thead>
          <th>品名</th>
          <th>數量</th>
          <th>單價</th>
        </thead>
        <tbody>
          <tr v-for="item in order.products" :key="item.id">
            <td class="align-middle">{{ item.product.title }}</td>
            <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
            <td class="align-middle text-right">{{ item.final_total }}</td>
          </tr>
        </tbody>
        <tfoot>
          <tr>
            <td colspan="2" class="text-right">總計</td>
            <td class="text-right">{{ order.total }}</td>
          </tr>
        </tfoot>
      </table>

      <table class="table">
        <tbody>
          <tr>
            <th width="100">Email</th>
            <td>{{ order.user.email }}</td>
          </tr>
          <tr>
            <th>名前</th>
            <td>{{ order.user.name }}</td>
          </tr>
          <tr>
            <th>電話番号</th>
            <td>{{ order.user.tel }}</td>
          </tr>
          <tr>
            <th>届け先</th>
            <td>{{ order.user.address }}</td>
          </tr>
          <tr>
            <th>お支払い</th>
            <td>
              <span v-if="!order.is_paid">尚未付款</span>
              <span v-else class="text-success">付款完成</span>
            </td>
          </tr>
        </tbody>
      </table>
      <div class="text-center" v-if="order.is_paid === false">
        <button class="btn btn-5" style="width:45vw">確認</button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
    data(){
        return{
            orderId:'',
            order: {
                user: {},
            },

        }
    },
    methods: {
        getOrder() {
        const vm = this;
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${vm.orderId}`;
        vm.isLoading = true;
        this.$http.get(url).then((response) => {
            vm.order = response.data.order;
            vm.isLoading = false;
            });
            
        },
        payOrder() {
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${vm.orderId}`;
            vm.isLoading = true;
            this.$http.post(url).then((response) => {
                console.log(response);
                if (response.data.success) {
                vm.getOrder();
                this.$bus.$emit('messsage:push','お支払い完成です！','success')
                }
                vm.isLoading = false;
            });
        }
    },
    created(){
        //取得網址上自定義的orderId
        this.orderId = this.$route.params.orderId;
        this.getOrder();
    }
}
</script>


